nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 90px;
}



.navbar-menu-container {
    display: none;
}

.navbar-links-container a {
    margin-right: 3rem;
    text-decoration: none;
    color: black;
    font-size: 1.1rem;
    font-weight: 600;
}

.navbar-cart-icon {
    font-size: 1.15rem;
}

.navbar-menu-container svg {
    font-size: 1.5rem;
    cursor: pointer;
}

@media (max-width:1000px) {
    .navbar-links-container a {
        margin-right: 1rem;
        font-size: 1rem;
    }

    .primary-button {
        font-size: 1rem;
    }
}

@media(max-width:800px) {
    .nav-logo-container {
        max-width: 140px;
    }

    .navbar-links-container {
        display: none;
    }

    .navbar-menu-container {
        display: flex;
    }
}