.home-container {
    margin: 0rem 3rem;
}

.home-banner-container {
    position: relative;
    display: flex;
    padding-top: 3rem;
}

.home-bannerImage-container {
    position: absolute;
    top: -100px;
    right: -170px;
    z-index: -2;
    max-width: 700px;
}

.home-image-section {
    max-width: 600px;
    flex: 1;
}

.home-text-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

@media (max-width:1000px) {
    .home-bannerImage-container {
        max-width: 600px;
    }
}


@media (max-width:800px) {
    .home-bannerImage-container {
        max-width: 450px;
    }

    .home-banner-container {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
    }

    .home-image-section {
        width: 100%;
        max-width: 400px;
    }

    .primary-heading {
        text-align: center;
        max-width: 90%;
    }

    .primary-text {
        text-align: center;
        max-width: 80%;
    }

    .home-text-section {
        justify-content: center;
        align-items: center;
        margin-top: 4rem;
    }

    .secondary-button {
        font-size: 1rem;
        padding: 0.8rem 2rem;
        cursor: pointer;
    }
}


@media (max-width:600px) {
    .home-bannerImage-container {
        display: none;
    }
}