.about-section-container {
    margin: 20rem 2rem 0rem;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-background-image-container {
    position: absolute;
    left: -150px;
    z-index: -2;
}

.about-section-image-container {
    flex: 1;
    margin-right: 3rem;
}

.about-section-text-container {
    flex: 1;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.about-buttons-container {
    margin-top: 2rem;
    display: flex;
}

.watch-video-button {
    margin-left: 2rem;
    background-color: transparent;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    color: #484848;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
}

.watch-video-button svg {
    font-size: 3rem;
    margin-right: 1rem;
}


@media (max-width:800px) {
    .about-section-container {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .about-section-image-container {
        width: 100%;
        max-width: 400px;
        margin-right: 0rem !important;
    }

    .about-section-text-container {
        justify-content: center;
        align-items: center;
        margin-top: 4rem;
    }

    .about-section-container {
        margin-top: 5rem !important;
    }

    .about-buttons-container {
        justify-content: center;
        flex-direction: column;
    }

    .watch-video-button {
        margin-left: 0rem !important;
        margin-top: 1rem;
        font-size: 1rem !important;
    }

    .watch-video-button svg {
        margin-right: 0.5rem !important;
    }
}

@media (max-width:600px) {
    .about-background-image-container {
        display: none;
    }
}